export default {
	props:{
		tabs: Array,
		value: { // 当前显示的下标 (使用v-model语法糖: 1.props需为value; 2.需回调input事件)
			type: [String, Number],
			default(){
				return 0
			}
		},
		fixed: Boolean // 是否悬浮,默认false
	},
	computed: {
		lineLift() {
			return 100/this.tabs.length*(this.value + 1) - 100/(this.tabs.length*2) + '%'
		}
	},
	methods: {
		getTabName(tab){
			return typeof tab === "object" ? tab.name : tab
		},
		tabClick(i){
			if(this.value!=i){
				this.$emit("input",i);
				this.$emit("change",i);
			}
		}
	}
}
